.about {
    padding-left: 50px;
    color: var(--text);
    background-color: var(--light);
    height: 100vh;
    /* opacity: 0.3; */
    /* z-index: -10; */
}

#full-stack {
    color: rgb(202, 44, 70);
    /* text-shadow: -2px 2px 2px rgb(13 171 171); */
    font-family: 'Archivo Black';
    font-size: 1.6rem;
    display: inline-block;
    /* font-weight: 900; */
    margin: 0 10px;
    transition: 0.3s all ease;
}


#passion {
    display: block;
    margin-top: 2rem;
    font-size: 1.1rem;
    font-family: 'Open Sans', sans-serif;
}


#hobbies {
    display: block;
    margin-top: 2rem;
    font-size: 1.4rem;
}

#future {
    display: block;
    margin-top: 2rem;
    font-size: 1.2rem;
    color: teal;
    font-weight: 700;
}

#hello {
    font-family: 'Button';
    /* color: rgb(44, 67, 99); */
    /* font-weight: 700; */
    /* letter-spacing: 3px; */
    font-size: 3.5rem;
    margin-right: 13px;

}

.skill-container {
    width: 500px;
    padding: 20px 20px;
    border-radius: 7px;
    background-color: var(--dark);
    margin: 25px 0;
    transition: 1s all ease;
}

.skill-container img {
    transition: 0.3s all ease-in-out;
}

.skill-container img:hover {
    transform: scale(1.1);
}

@media only screen and (max-width: 600px) {
    .about {
        padding-left: 0;
    }
}