.showcase-container {
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
    /* height: 100vh; */
}

#project-block {
    position: relative;
    overflow: hidden;
    background-color: var(--dark);
    color: white;
    padding: 0.8rem;
    margin-left: 0.7rem;
    margin-bottom: 1rem;
    transition: 0.3s all ease;
}

.project-title {
    font-size: 25px;
    margin-bottom: 10px;
    border-bottom: 2px solid rgba(234, 234, 234, 0.533);

}

.project-img {
    height: 250px;
    transition: 0.45s all ease;
}

.project-desc {
    position: absolute;
    font-family: 'Open Sans';
    font-weight: 500;
    font-size: 1.1rem;
    bottom: 40px;
    margin: 0 10%;
}

.hover-container {
    transform: translateY(100%);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    text-align: center;
    transition: 0.3s all ease;

}

.hover-btn-container {
    position: absolute;
    top: 30%;
    left: calc(50% - 50px);
}

.hosted-link {
    width: 40px;
    height: 35px;
    margin-left: 20px;
    transition: 0.3s all ease;
    background-color: var(--accent);
    color: white;
    border: 1px solid white;
    border-radius: 3px;
    transition: 0.25s all ease;
}

.hosted-link:hover {
    transform: scale(1.2);
    border-radius: 10px;
}

.github-link {
    width: 40px;
    height: 35px;
    color: white;
    background-color: var(--dark);
    border-radius: 3px;
    border: 1px solid white;
    transition: 0.25s all ease;
}

.github-link:hover {
    transform: scale(1.2);
    border-radius: 10px;
}

a {
    text-decoration: none;
}

#project-block:hover .project-img {
    /* filter: ; */
    filter: brightness(0.5) blur(5px);
}


#project-block:hover .hover-container {
    /* display: block; */
    transform: translateY(0);
}

