@keyframes fadeUp {
    0% {
        opacity: 0;
        transform: translateY(-300px);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.right-section {
    /* height: 100vh; */
    animation: fadeUp 1.5s ease 1;
    font-size: 1.5rem;
    margin-left: 400px;
    padding: 50px 1rem;
    transition: all 0.5s ease;
    background-color: var(--light);
    color: var(--text);
}

#unsupported {
    text-align: center;
    margin-top: 50vh;
    color: rgb(49, 49, 49);
    letter-spacing: 1px;
    word-spacing: 2px;
}

@media only screen and (max-width: 600px) {
    .right-section {
        margin-left: 0;
        padding: 20px 0;
    }
}

