.socials {
    height: 100vh;
}

.socials h1 {
    font-family: 'Archivo Black';
    text-align: center;
    font-weight: 500;
}

.me {
    background-color: var(--accent);
    padding: 0 10px;
    border-radius: 10px;
    color: var(--light);
    transition: 0.5s all ease;
}

.icons img {
    width: 150px;
    transition: 0.5s all ease;
}

.icons img:hover {
    transform: scale(1.3);
}

.icons {
    display: flex;
    margin-top: 15%;
    justify-content: space-evenly;
}