@import url('https://fonts.googleapis.com/css2?family=Anton&family=Archivo+Black&family=Karla:wght@300&family=Open+Sans&family=Sacramento&display=swap');

:root {
  --accent: #61dafb;
  --light: white;
  --text: rgb(53, 53, 53);
  --dark: #282c34;
}

@font-face {
  font-family: 'GeneralSans-Regular';
  src: url('./fonts/GeneralSans-Regular.woff2') format('woff2'),
    url('./fonts/GeneralSans-Regular.woff') format('woff'),
    url('./fonts/GeneralSans-Regular.ttf') format('truetype');
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: 'Line';
  src: url('./fonts/line.otf');
}

@font-face {
  font-family: 'Iso';
  src: url('./fonts/3diso-bold.ttf');
}

@font-face {
  font-family: 'Button';
  src: url('./fonts/unbutton.ttf');
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'GeneralSans-Regular';
}