.toggle {
    position: absolute;
    top: 0;
    right: 0;
    margin: 20px;
    font-family: 'Karla';
    font-weight: 900;
    padding: 4px 10px;
    background-color: transparent;
    color: var(--dark);
    border-radius: 3px;
    /* box-shadow: 0px 0px 10px 0.1px var(--dark); */
    border: none;
    transition: 0.5s all ease;
    /* flex-direction: row; */
    /* align-items: center; */
}

/* .toggle:hover {
    background-color: var(--dark);
    color: var(--light);
} */


.toggle:active {
    transform: scale(0.9);
}