@keyframes slide {
    0% {
        transform: translateX(-100%);
    }

    100% {
        transform: translateX(0);
    }
}

.side-bar {
    background-color: var(--dark);
    position: fixed;
    width: 400px;
    height: 100%;
    animation: slide 1.2s ease;
    /* padding-left: 50px; */
    padding: 0 50px;
    transition: all 0.5s ease;
    /* text-align: center; */
}

.side-box {
    margin-top: 50px;
}

.name {
    margin-bottom: 100px;
    font-family: 'Archivo Black', sans-serif;
    color: var(--light);
    font-weight: 400;
    font-size: 40px;
    padding-bottom: 20px;
    border-bottom: 15px solid var(--light);
    transition: 0.5s all ease;
}

.nav-items {
    margin-top: 150px;
}

.nav-items li {
    list-style: none;
    margin-bottom: 20px;
}

.nav-link {
    text-decoration: none;
    font-family: 'Karla', sans-serif;
    text-align: center;
    font-size: 1.75rem;
    font-weight: 100;
    color: var(--light);
    transition: 0.1s all ease;
}

.nav-link:hover {
    color: rgb(0, 79, 79);
}

.active {
    color: var(--accent);
    font-weight: 600;
}

@media only screen and (max-width: 600px) {
    .side-bar {
        display: none;
    }
}